.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-right: 50px;
}

.profile-card {
  display: flex;
  padding: 48px 30px;
  background-color: #fff;
  border-radius: 25px;
}

.details-container {
  display: flex;
  width: 100%;
}

.left-con {
  width: 50%;
  margin-right: 28px;
}

.right-con {
  width: 50%;
}

p {
  color: #718ebf;
  font-family: Inter;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.pro-field {
  border-radius: 15px;
  border: 1px solid #dfeaf2;
  background: #fff;
  padding: 14px 20px;
  margin-top: 10px;
}

label {
  padding-bottom: 10px;
  color: #505887;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

.right-con div {
  margin-bottom: 20px;
}

.left-con div {
  margin-bottom: 20px;
}
